import React, { useEffect, useRef, useState } from 'react';
import './Display.scss';
import apple from '../../../images/apple.png';
import apple2 from '../../../images/apple2.png';
import AppleSlice from '../AppleSlice/AppleSlice';

// 'input' se utiliza para registrar los ingresos del teclado
// 'calculate' se utiliza para mostrar la expresin de la operacin despus de darle al botn igual.
type Props = {
    input: string;
    calculate: string;
};

const Display = ({ input, calculate }: Props) => {
  
  // Relacin de aspecto de la imagen usada (ancho/alto)
  const appleImageRatio = 9/11;

  // Mximas manzanas a mostrar en el display por contenedor
  const maxDisplayItems = 100;

  // Nmero de decimales para los redondeos
  const numDecimals = 2;

  const [operation, setOperation] = useState<string[]>([]);
  const [numColumnsArr, setNumColumnsArr] = useState<number[]>([]);  
  // Creamos una referencia mutable a un elemento de la interfaz de usuario con useRef
  const applesImagesElementRef = useRef<HTMLDivElement>(null);
  
  // Separamos cada elemento de la operacin en un array utilizando una expresin regular que busca los operadores
  // (+, , , , y =) como separadores. Cada nmero u operador se almacena en una posicin del array Operation.
  useEffect(() => {
    setOperation(input.split(/([+=])/));
  }, [input]);  
 
  // Usamos useEffect para calcular el nmero de columnas de los contenedores de manzanas dependiendo de las dimensiones del elemento referenciado
  // Los clculos se ejecutan cada vez que cambie el array 'operation'
  useEffect(() => {
    if (applesImagesElementRef.current) {     

      // Obtenemos el ancho y el alto del contenedor de las manzanas
      // Todos los contenedores de manzanas tienen las mismas dimensiones por lo tanto solo se tiene referencia al primero
      const width =  applesImagesElementRef.current.offsetWidth;
      const height =  applesImagesElementRef.current.offsetHeight;
      const area = width*height;

      // Creamos una copia del arreglo actual de columnas
      const updatedNumColumnsArr = [...numColumnsArr];
      
      // Iteramos sobre la operacin que determina el nmero de columnas por contenedor de manzanas
      // Usamos la expresin i+=2 para saltar unicamente sobre los datos numricos de la operacin
      for (let i=0; i < operation.length; i+=2) {
        const itemOperation = Number(operation[i]);
        let numColumns: number = 1;

        //Condicional para limitar nmero de items por contenedor
        if (itemOperation<=maxDisplayItems && itemOperation>0) {
          // Redondeamos al entero ms proximo hacia arriba de operation[i]
          const integerPartOperation = Math.ceil(itemOperation);
          // Obtenemos el rea y el lado de cada manzana
          const appleArea = area/integerPartOperation;
          const appleSide = Math.sqrt(appleArea);

          /* Calculamos el nmero de columnas necesarias
          Si el alto del contenedor de manzanas es menor al alto de la manzana con su respectivo ajuste de aspecto 
            se asigna un nmero de columnas igual que el total de manzanas que registre la operacin en i.
          De lo contrario, se hace el calculo de columnas dividiendo el ancho del contenedor de manzanas por
            el ancho de la manzana con su respectivo ajuste de aspecto*/
          if (height < (appleSide/appleImageRatio)) {
            numColumns = integerPartOperation;          
          } else {
            numColumns = Math.round(width/(appleSide*appleImageRatio));
          };
        };        

        //Actualizamos el array con el nmero de columnas por contenedor de manzanas (i)
        updatedNumColumnsArr[i] = numColumns;
        setNumColumnsArr(updatedNumColumnsArr)    
      };    
    };
  }, [operation]);
  
  // Genera un objeto de estilos dinmico para el nmero de filas y columnas de cada contenedor de manzanas
  const applesImagesElementStyles = (i: number) => {
    const itemOperation = Number(operation[i]);
    let rows:number = 1;

    // Redondeamos al entero ms proximo hacia arriba de operation[i]
    const integerPartOperation = Math.ceil(itemOperation);

    // Calculamos el nmero de filas (redondeamos hacia arriba)
    if (itemOperation<=maxDisplayItems) {
      rows = Math.ceil(integerPartOperation / numColumnsArr[i])
    } else if (itemOperation%1>0) {
      rows = 2; // Fila adicional para la parte decimal de la operacin
    }

    return {
      gridTemplateColumns: `repeat(${numColumnsArr[i]}, 1fr)`,
      gridTemplateRows: `repeat(${rows}, 1fr)`
    }
  }
  
  // Mapeamos el array de 'operation' para generar los elementos de la calculadora  
  const operationElements = operation.map((item, index) => {
    if (/^[+=]$/.test(item)) {
      // Si el item es un operador, se genera un div con la clase 'operatorElement' y el contenido del operador
      return (
        <div
        key={`operator-${index}-${item}`}
        className='operatorElement'>
          {item}
        </div>
      );
    } else {
      // Si el item es un nmero, se genera un elemento JSX con la/las manzana/s

      let apples:JSX.Element | JSX.Element[];
      const number = Number(item);

      if (number<=maxDisplayItems && number>0 ) {
        // Si item es menor o igual al nmero mximo de manzanas a renderizar y distinto de '0', se genera un array
        apples = Array.from({ length: number}, (_, i) => (
          <img
          key={`apple-${index}-${item}-${i}`}
          src={apple}
          alt="Apple"/>
        ));
      } else if ( (operation.length==1 && item=='0' && !calculate) || item == '') {
        // No renderizar nada cuando no se ha hecho ningn calculo o no se a ingresado algn nmero
        apples = [];
      } else {
        // De lo contrario se genera una unica manzana con el contenido del item superpuesto
        // Si es 0 o menor el nmero se aplica la clase numberZero a img
        apples = (
          <div className='appleContainer'>
            <img className={number<=0 ? 'negativeNumber' : ''} src={apple} alt="Apple"/>
            <p className='numberOverlay'> {number<=0 && number%1!=0 ? number.toFixed(numDecimals) : Math.trunc(number)} </p>
          </div>
        )
      }

      // Se retorna un div que contiene las imgenes de manzanas
      // Se establece la propiedad 'ref' solo en el primer contenedor de manzanas para obtener una referencia a l
      // Se le asignan los estilos dinmicos de 'gridTemplateColumns' usando 'applesImagesElementStyles'
      // Se renderiza la imagen apple en proporcin a la fraccin decimal si el nmero no es entero (Number(item)%1 > 0)
      return (
        <div
        key={`apples-${index}-${item}`}
        className='applesImagesElement'
        ref={index === 0 ? applesImagesElementRef : null}
        style={applesImagesElementStyles(index)}>          
          {apples}
          {Number(item)%1 > 0 && <AppleSlice img={apple2} alt="Apple" decimalFraction={Number(item)%1}></AppleSlice>}
        </div>
      );
    };
  });

  /**
   * Esta funcin redondea el primer nmero de una expresin a un nmero especfico de decimales.
   * @param expression La expresin matemtica en forma de string o array de strings
   * @param numDecimals El nmero de decimales a redondear.
   * @returns La expresin con el primer nmero redondeado al nmero de decimales especificado.
   */
  const roundFirstNumber = (expression:string|string[], numDecimals_:number) => {

    const calculateArray = Array.isArray(expression) ? expression : expression.split(/([+=])/);

    return (Number(calculateArray[0])%1==0
      ? expression
      : Number(calculateArray[0]).toFixed(numDecimals_) + calculateArray.slice(1).join('')
    )
  }

  return (
    <div className='displayContainer'>

      <div className='displayContainer-apples'>
        {operationElements}
      </div>
      
      <div className='displayContainer-calculate-input'>
        <div className='displayContainer-calculate'>
          {calculate && roundFirstNumber(calculate, numDecimals)}
        </div>    
        <div className='displayContainer-input'>
          {roundFirstNumber(operation, numDecimals)}
        </div>
      </div>

    </div>
  )
}

export default Display;