import React, { useState } from 'react';
import { evaluate, fraction} from 'mathjs'; //Paquete matemtico (npm install mathjs)
import Display from './Display/Display';
import Keypad from './Keypad/Keypad';
import './Calculator.scss';

const Calculator = () => {

  const [input, setInput] = useState<string>("0");
  const [calculate, setCalculate] = useState<string>("");

  //Funcin para calcular resultado
  const calculateResult = () => {
    
    // Reemplaza los operadores utilizados en el proyecto con los operadores aceptados por la funcin `evaluate()`
    const replaceOperators = (toReplace: string) => {

      // Objeto de operadores(key) con su remplazo(value)
      const operatorsToReplace: {[key:string]:string} = {
        '':'-',
        '':'*',
        '':'/'
      };

      // Expresin regular para buscar operadores a reemplazar
      const regex = new RegExp(Object.keys(operatorsToReplace).join('|'), 'g');
      
      return toReplace.replace(regex, (operator) => operatorsToReplace[operator]);
    };

    setCalculate(input + "=");// Establecemos la expresin de la operacin actual    
    setInput(evaluate(replaceOperators(input)).toString()); //Funcin del paquete mathjs que nos permite calcular el resultado, nos devuelve un number
  }

  //Funcin para agregar operadores al clculo
  const addOperator = (operator: string) => {   
    setCalculate(""); //Limpiamos la expresin de la operacin anterior
    /* Validacin para evitar la presencia de operadores consecutivos en el clculo.
    Si el ltimo carcter del input es un operador, se reemplaza el operador anterior por el nuevo operador.
    De lo contrario, se agrega el operador al input.*/
    // Nota: /[+]$/ expresin regular
    if (/[+]$/.test(input)) {     
      setInput(input.slice(0,-1) + operator);
    } else {
      setInput(input + operator);
    };
  };

  //Funcin para agregar nmeros al clculo
  const addNumber = (number: string) => {
    if (calculate) setCalculate(""); //Limpiamos la expresin de la operacin anterior
    input === "0" ? setInput(number) : setInput(input + number);
  };

  //Funcin para borrar los datos
  const clear = () => {  
    /*Esta funcin se encarga de borrar la entrada del usuario (input) y/o la expresin de la operacin (calculate).
    Si calculate tiene algn valor, se reinicia a una cadena vaca e input se establece en "0".
    De lo contrario, se elimina el ltimo dgito del input (a menos que tenga solo un dgito, en cuyo caso se establece en "0").*/
    if (calculate) {      
      setCalculate("");   
      setInput("0");
    }
    else {
      setInput(input.length > 1 ? input.slice(0, -1) : "0");
    };
  };

  const handleInput = (value: string) => {

    switch (value) {
      case "C":
        clear();
        break;
      case "=":
        calculateResult();
        break;
      case "+":
      case "":
      case "":
      case "":
        addOperator(value);
        break;
      default:
        addNumber(value);
    };
  };

  return (
    <div className='calculatorContainer'>
      <div className="calculatorContainer-display">
        <Display input= { input } calculate= { calculate }/>
      </div>
      <div className='calculatorContainer-keypad'>
        <Keypad _handleKeypadInput={ handleInput }/>
      </div>
    </div>
  );
};

export default Calculator;